import  { Component } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import '../css/App.css';
import trophy from '../img/trophy.png'
type MyProps = { 
    scoreBoard?: any,
    totalScoreBoard?: any,
};
type MyState = { 
};
const theme = createTheme({
    palette: {
        mode: "dark"
    }
});
class ScoreBoard extends Component <MyProps, MyState>{
    
    constructor(props: MyProps) {
        super(props);
        this.state = {
        };
    }
    
    render() {
        const { scoreBoard, totalScoreBoard } = this.props;
        let scores = scoreBoard ? scoreBoard.scores : totalScoreBoard;
        let length = 14;

        scores?.map((score: any, index: number) => {
            if(index === 0 && score.score){
                score.color = "#FFD700";
                score.placement = 1;
            }
            if(index > 0 && score.score){
                if(score.score === scores[index-1].score && score.trophies.length === scores[index-1].trophies.length){
                    score.color = scores[index-1].color;
                    score.placement = scores[index-1].placement
                }else{
                    if (index === 1) {
                        score.color = "#C0C0C0"
                        score.placement = 2;
                    }else if(index === 2){
                        score.color = "#CD7F32"
                        score.placement = 3;
                    }
                }
            }
        })

        return (
            <ThemeProvider theme={theme}>
                <Card sx={{ width: 400, maxWidth: '100vw', margin: 1, boxShadow: '8px 10px 25px 1px #000000', borderRadius: '10px' }}>
                    <CardContent>
                        <Typography 
                            gutterBottom 
                            variant="h6" 
                            className="smile"
                            component="div">
                                <div >
                                    <span className = {scoreBoard ? 'roundHeader': (totalScoreBoard ? 'totalHeader': '')}> 
                                        {
                                        (scoreBoard && scores[0].score)
                                        ? 
                                        'Runde resultat'
                                        :
                                        (scoreBoard)
                                            ?
                                            'Levert tippekuppong'
                                            :
                                            ''
                                        }
                                        { 
                                        (totalScoreBoard)
                                            ? 
                                            'Toppliste'
                                            : 
                                            ''
                                        } 
                                    </span> 
                                </div>
                            </Typography>
                            { scores ? 
                                scores.map((userScore: any, index: number) => (  
                                    <Typography 
                                        key={userScore.userId}
                                        variant="body1" 
                                        color={userScore.color}>
                                        <div className="scoreBoard">
                                            <div>
                                                { 
                                                    userScore.score ?
                                                    userScore.placement+'. '
                                                    :
                                                    '' } {
                                                    userScore.userName.substring(0, length) + (userScore.userName.length >= length ? '...' : '')
                                                    } 
                                            </div>
                                            {
                                                (scoreBoard && userScore.score && index === 0) 
                                                ? 
                                                    <div><img src={trophy} className="trophyLogo" alt="logo" /></div>
                                                : 
                                                    (totalScoreBoard && userScore.trophies.length > 0) 
                                                    ?
                                                        <div>
                                                            {userScore.trophies.map((t: any) => (  
                                                            <img key={t} src={trophy} className="trophyLogo" alt="logo" />
                                                        ))}
                                                        </div>
                                                    :
                                                        <div/>
                                            }
                                            <div>
                                            {userScore.score ? userScore.score : ''} 
                                            </div>
                                        </div>
                                    </Typography>
                                ))  : 
                                '' 
                            }
                    </CardContent>
                </Card>
            </ThemeProvider>
        );
    }
}

export default ScoreBoard;

