import  { Component } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import '../css/App.css';
import l1072363 from '../img/1072363.png'
import l1075072 from '../img/1075072.png'
import l1076796 from '../img/1076796.png'
import bot from '../img/bot_team_logo.png'

type MyProps = { 
    match: any, 
    roundId: string, 
};
type MyState = { 
};
const theme = createTheme({
    palette: {
        mode: "dark"
    }
});
class RoundBoard extends Component <MyProps, MyState>{
    
    constructor(props: MyProps) {
        super(props);
        this.state = {
        };
    }

    render() {
        const { match } = this.props;
        if(match.MatchResult?.guesses){
            match.MatchResult.guesses = match.MatchResult.guesses.sort((a: any, b: any) => {
                if(a.points){
                    return (a.points < b.points ? -1 : 1)
                }
                return (a.userName < b.userName ? -1 : 1)
            });
        }
        const results = match.MatchResult;
        const teamArray = ['l1072363', 'l1075072', 'l1076796']
        const homeId = 'l' + match.HomeTeam.HomeTeamID;
        const awayId = 'l' + match.AwayTeam.AwayTeamID;
        let homeSrc = bot;
        if(teamArray.includes(homeId)){
            if(homeId === 'l1072363'){
                homeSrc = l1072363
            }else if(homeId === 'l1075072'){
                homeSrc = l1075072
            }else {
                homeSrc = l1076796
            }
        }
        let awaySrc = bot;
        if(teamArray.includes(awayId)){
            if(awayId === 'l1072363'){
                awaySrc = l1072363
            }else if(awayId === 'l1075072'){
                awaySrc = l1075072
            }else {
                awaySrc = l1076796
            }
        }
        let homeName = match.HomeTeam.HomeTeamName;
        let awayName = match.AwayTeam.AwayTeamName;
        let text = homeName + ' vs ' + awayName;
        let length = 30;
        let oWidth = 400 - ((16+8)*2);
        let canvas = document.createElement('canvas');
        let ctx = canvas.getContext("2d");
        if(ctx){
            ctx.font = "italic small-caps bold  1.3rem Arial";        
        }
        let width = ctx!.measureText(text).width;
        while(width > oWidth){
            length = length -1;
            homeName = homeName.substring(0, length);
            awayName = awayName.substring(0, length);         
            text = homeName + '.. vs ' + awayName + '..';
            width = ctx!.measureText(text).width;
        } 

        return (
            <ThemeProvider theme={theme}>
                <Card sx={{ width: 400, maxWidth: '100vw', margin: 1, boxShadow: '8px 10px 25px 1px #000000', borderRadius: '10px' }}>                
                    <CardContent>
                        <Typography 
                            gutterBottom 
                            sx={{ margin: 0, padding: 0 }}
                            variant="h6" 
                            className="smile"
                            component="div">
                                <div >
                                    <span className = {homeSrc === bot ? '': 'playerColor'}> { homeName.substring(0, length) + (homeName.length >= length ? '..' : '')} </span>
                                    <span className = 'grey'> vs </span> 
                                    <span className = {awaySrc === bot ? '': 'playerColor'}> { awayName.substring(0, length) + (awayName.length >= length ? '..' : '')}</span>
                                </div>
                            </Typography>
                            <Typography variant="body1" 
                                color={results?.matchHomeScore ? 'success.main' : 'error.main'}>
                                    <div className="testyMctest">
                                        <div>
                                        {results?.matchHomeScore ? 'Kampresultat' : 'Resultater ikke klare.'} 
                                        </div>
                                        <div>
                                        {results?.matchHomeScore ? results.matchHomeScore + ' - ' + results.matchAwayScore : 'X - X'} 
                                        </div>
                                        <div>
                                        {results?.matchHomeScore ? 'Poeng' : ''} 
                                        </div>
                                    </div>
                                </Typography>
                        { results ? 
                            results.guesses.map((guess: any, index: number) => (  
                            <Typography key={guess.userId} variant="body2" 
                                color="text.secondary">
                                    <div className="testyMctest">
                                        <div>
                                        {(index+1) + '. ' + guess.userName.substring(0, (length+5)) + (guess.userName.length > (length+5) ? '...' : '')} 
                                        </div>
                                        <div>
                                        {guess.homeScore} - {guess.awayScore}
                                        </div>
                                        <div>
                                        {guess.points}
                                        </div>
                                    </div>
                                </Typography>
                          ))  : '' }
                    </CardContent>
                </Card>
            </ThemeProvider>
        );
    }
}

export default RoundBoard;

