import  React, { Component } from "react";
import '../css/App.css';
import ScoreBoard from "./ScoreBoard";
import RoundBoard from "./RoundBoard";
import { Match } from "../model/hattrick/Match";
import helpers from "../helpers/APIHelper";
import { gsap } from "gsap";

type MyProps = { 
  matches: Match[], 
  roundId: any,
  currentUser: any
};
type MyState = { 
  totalScoreBoard: any, 
  scoreBoard: any,
  roundGuesses: any,
  allRoundsGuesses: any,
  users: any,
};
class Results extends Component <MyProps, MyState>{
  private boxRef: React.RefObject<HTMLDivElement>;

    constructor(props: MyProps) {
        super(props);
        this.boxRef = React.createRef();
        this.state = {
          totalScoreBoard: null, 
          scoreBoard: null,
          roundGuesses: null, 
          allRoundsGuesses: null,
          users: null
        };
    }
    
    componentDidMount(){
      gsap.fromTo(this.boxRef.current, {opacity: 0}, { opacity: 1, duration: 1 });
    }

    async componentWillMount() {
      const { currentUser } = this.props;
      const { roundId } = this.props;
      const allRoundsGuesses = await helpers.fetchAllMatches(currentUser.userToken);
      const users = await helpers.getAllUsers(currentUser.userToken)
      let roundGuesses = allRoundsGuesses.find((roundGuesses: any) => {
        return roundGuesses.roundId === roundId;
      })
      this.setState({
        allRoundsGuesses: allRoundsGuesses,
        roundGuesses: roundGuesses,
        users: users
      }, () => {
        this.updateTotalScoreBoard();
      })
    }

    componentDidUpdate = () => {
      const { roundId } = this.props;
      let { roundGuesses, allRoundsGuesses } = this.state;
      if(roundId !== roundGuesses.roundId){
        roundGuesses = allRoundsGuesses.find((roundGuesses: any) => {
          return roundGuesses.roundId === roundId;
        })
        this.setState({
          roundGuesses: roundGuesses
        }, () => {
          this.updateThisScoreBoard();
        })
      }
    }

    calculateScore = (home: any, away: any, guess: any) => { 
      if(home === null || home === undefined || isNaN(home)){
        return -1;
      }
      home = parseInt(home);    
      away = parseInt(away);
      guess.homeScore = parseInt(guess.homeScore);
      guess.awayScore = parseInt(guess.awayScore);
      
      const homeWin = home > away;
      const awayWin = away > home;
      const tie = away === home;
      const guessHomeWin = guess.homeScore > guess.awayScore;
      const guessAwayWin = guess.homeScore < guess.awayScore;
      const guessTie = guess.homeScore === guess.awayScore;
      
      if(home === guess.homeScore && away === guess.awayScore){
          return 5;
      } else if (tie && guessTie){
          return 4;
      }else if ((homeWin && guessHomeWin) ||(awayWin && guessAwayWin) ){
          if((home - away) === (guess.homeScore - guess.awayScore)){
              return 3;
          }else if(home === guess.homeScore || away === guess.awayScore){
              return 3;
          }
          return 2;
      }else if(home === guess.homeScore || away === guess.awayScore){
              return 1;
      }else {
          return 0
      } 
    };

    updateTotalScoreBoard = async () => {
      const { allRoundsGuesses, users } = this.state;
      let totalScoreBoard = users.map((user: any) => {
            return {
              userId: user.userId, 
              userName: user.userName, 
              score: 0, 
              trophies: []
            }
      })
      allRoundsGuesses.forEach((roundGuesses: any) => {
        roundGuesses?.matches.map((match: any) => {
          match.guesses.map((guess: any) => {
            const points = this.calculateScore(match.matchHomeScore, match.matchAwayScore, guess) ?? -1
            if(points >= 0){
              totalScoreBoard.map((user: any) => {
                if(user.userId === guess.userId){
                  user.score += points;
                }
                return user;
              })
              guess.points = points;
            }
            return guess;
          })
          return match;
        })
        totalScoreBoard = totalScoreBoard.sort((a: any, b: any) => (a.score < b.score ? 1 : -1));
        this.setState({totalScoreBoard: totalScoreBoard}, () => {
          for (let i = 1; i < 15; i++) {
            let roundGuesses = allRoundsGuesses.find((roundGuesses: any) => {
              return roundGuesses.roundId === (''+i);
            })
            this.updateScoreBoard((''+i), roundGuesses);
          }
          this.updateThisScoreBoard()
        })
      })
    }
  
    updateThisScoreBoard = () => {
      const { roundId } = this.props;
      const { allRoundsGuesses } = this.state;

      let roundGuesses = allRoundsGuesses.find((roundGuesses: any) => {
        return roundGuesses.roundId === roundId;
      })
      this.updateScoreBoard(roundId, roundGuesses);
    }

    updateScoreBoard = (roundId: string, roundGuesses: any) => {
      let { totalScoreBoard } = this.state;
      if(roundGuesses?.matches.length === 0){
        this.setState({scoreBoard: null})
        return
      }
      let scoreBoard = {
        round: roundId,
        scores: roundGuesses?.matches[0].guesses.map((guess: any) => {
            return {userId: guess.userId, userName: guess.userName, score: 0}
        })
      }
      roundGuesses?.matches.map((match: any) => {
        match.guesses.map((guess: any) => {
          const points = this.calculateScore(match.matchHomeScore, match.matchAwayScore, guess) ?? -1
          if(points >= 0){
            scoreBoard.scores.map((user: any) => {
              if(user.userId === guess.userId){
                user.score += points;
              }
              return user;
            })
            guess.points = points;
          }
          return guess;
        })
        return match;
      })
      scoreBoard.scores = scoreBoard.scores.sort((a: any, b: any) => (a.score < b.score ? 1 : -1));
      //Set tophy
      totalScoreBoard = totalScoreBoard.map((user: any) => {
        if(user.userId === scoreBoard.scores[0].userId && scoreBoard.scores[0].score){
          let exists = user.trophies.find((trophy: any) => {
            return trophy.roundId === scoreBoard.round;
          })
          if(!exists){
            user.trophies = [...user.trophies, {roundId: scoreBoard.round}]
          }
        }
        return user;
      })
      this.setState({scoreBoard: scoreBoard, totalScoreBoard: totalScoreBoard})
    }

    render() {
        const { matches, roundId } = this.props;
        const { totalScoreBoard, roundGuesses, scoreBoard} = this.state;
        let matchDTO = matches.map((match: any) => {
          return {
            ...match, 
            MatchResult: roundGuesses?.matches.find((rMatch: any) => {
              return rMatch.matchId === match.MatchID;
            })
          }
        })

        return (
          <div ref={this.boxRef}>
            <div className="scoreBody">
              <ScoreBoard totalScoreBoard={totalScoreBoard} />
              {scoreBoard ? <ScoreBoard scoreBoard={scoreBoard} />: ''}
              </div>
            <div className="roundBody">
              {matchDTO.map((match: any) => (
                <RoundBoard key={match.MatchID} match={match} roundId={roundId}/>
              ))} 
            </div>
          </div>
        );
    }
}

export default Results;

