import  { Component } from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { Box, Button, TextField } from '@mui/material';
import { Match } from "../model/hattrick/Match";
import '../css/App.css';
import l1072363 from '../img/1072363.png'
import l1075072 from '../img/1075072.png'
import l1076796 from '../img/1076796.png'
import bot from '../img/bot_team_logo.png'
import stadium from '../img/basic_stadium.jpeg'
import celtic_stadium from '../img/celtic_stadium.jpg'
import talent_stadium from '../img/talent_stadium.jpeg'
import * as React from 'react';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert, { AlertProps } from '@mui/material/Alert';
import { gsap } from "gsap";

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const theme = createTheme({
    palette: {
        mode: "dark"
    }
});

type MyProps = { match: Match, roundId: string, mainRound: string };
type MyState = { open: boolean, saved: any };
class MatchCard extends Component <MyProps, MyState>{
    private boxRef: React.RefObject<HTMLDivElement>;

    constructor(props: MyProps) {
        super(props);
        this.boxRef = React.createRef();

        this.state = {
            open: false,
            saved: {
                message: "Ventetekst!",             
                severity: "info"
            }
        };
    }

    componentDidMount(){
        gsap.fromTo(this.boxRef.current, {opacity: 0}, { opacity: 1, duration: 2 });
    }

    handleClick = () => {
        this.setState({open:true})
    };

    handleClose = (event:any) => {
        if (event.reason === 'clickaway') {
            return;
        }
        this.setState({open:false})
    };

    handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        const { match, roundId } = this.props;
        const token = window.localStorage.getItem("token") as string;
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        // eslint-disable-next-line no-console
        const requestOptions = {
            method: 'POST',
            headers: { 
                'Content-Type': 'application/json',
                'x-auth-token': token
            },
            body: JSON.stringify({ 
                matchId: match.MatchID,
                userId: window.localStorage.getItem("id"),
                roundId: roundId,
                homeScore: data.get('home'),
                awayScore: data.get('away'), 
            })
        };
        fetch('https://hattrick-webapi.herokuapp.com/api/tipping/guess', requestOptions)
        .then(response => response.json())
        .then(data => {
            if(data === 1){
                this.setState({
                    saved: {
                        message: "Tipperesultat oppdatert!", 
                        severity: "info"
                    },
                    open:true
                })
            }else{
                this.setState({
                    saved: {message: "Tipperesultat levert!", severity:"success"},
                    open:true
                }) 
            }
            //onLogin(data)
        })
        .catch((error) => {
            this.setState({
                saved: {message: "Noe gikk galt! Kontakt Taddis!", severity:"error"},
                open:true
            }) 
        });
    };

    render() {
        const { open, saved } = this.state;
        const { match, roundId, mainRound } = this.props;

        const img = (match.HomeTeam.HomeTeamID === '1075072') ? 
            celtic_stadium :
            (match.HomeTeam.HomeTeamID === '1076796') ? 
                talent_stadium :
                stadium;
        const teamArray = ['l1072363', 'l1075072', 'l1076796']
        const homeId = 'l' + match.HomeTeam.HomeTeamID;
        const awayId = 'l' + match.AwayTeam.AwayTeamID;
        const prevRound: boolean = (+roundId) < (+mainRound);

        let homeSrc = bot;
        if(teamArray.includes(homeId)){
            if(homeId === 'l1072363'){
                homeSrc = l1072363
            }else if(homeId === 'l1075072'){
                homeSrc = l1075072
            }else {
                homeSrc = l1076796
            }
        }
        let awaySrc = bot;
        if(teamArray.includes(awayId)){
            if(awayId === 'l1072363'){
                awaySrc = l1072363
            }else if(awayId === 'l1075072'){
                awaySrc = l1075072
            }else {
                awaySrc = l1076796
            }
        }
        const weirdShit: any = match;
        let oWidth = window.outerWidth;
        let stadiumHeight = 250;
        if(oWidth < 700){
            stadiumHeight = 140;
        }
        
        return (
            <ThemeProvider theme={theme}>
                <Card ref={this.boxRef} sx={{ width: 500, margin:1, height:1, boxShadow: '8px 10px 25px 1px #000000', borderRadius: '10px'  }}>
                    <CardMedia
                        component="img"
                        sx={{height:stadiumHeight}}
                        image={img}
                        alt="celtic stadium"
                    />
                    <CardContent>

                        <Typography 
                            gutterBottom 
                            variant="h5" 
                            className="smile"
                            component="div">
                                <img src={homeSrc} className="Club-logo" alt="logo" />
                                <div className="hihi" >
                                    <span className = {homeSrc === bot ? '': 'playerColor'}>{ match.HomeTeam.HomeTeamName } </span>
                                    <br/>
                                    <span className = 'vsColor'> vs </span> 
                                    <br/>
                                    <span className = {awaySrc === bot ? '': 'playerColor'}> {match.AwayTeam.AwayTeamName}</span>
                                </div>
                                <img src={awaySrc} className="Club-logo" alt="logo" />
                        </Typography>
                        <Box component="form" onSubmit={this.handleSubmit} noValidate sx={{ mt: 1 }}>
                            <div className="test1">
                                <TextField
                                disabled={prevRound}
                                margin="normal"
                                id="home"
                                label="Hjemme"
                                name="home"
                                type="number"
                                inputProps={{ 
                                    pattern:"[0-9]*" 
                                }}
                                autoComplete="off"
                                />
                                <TextField
                                disabled={prevRound}
                                margin="normal"
                                id="away"
                                label="Borte"
                                name="away"
                                type="number"
                                inputProps={{ 
                                    pattern:"[0-9]*" 
                                }}
                                autoComplete="off"
                            />
                            </div>
                            <Button
                                disabled={prevRound}
                                type="submit"
                                variant="contained"
                                color="success"
                                sx={{ mt: 1, mb: 0, width: '60%' }}
                            >
                                Tipp
                            </Button>
                        </Box>
                        <Typography 
                            variant="body2" 
                            color="text.secondary">
                            { weirdShit.AwayGoals ? 'Score:'+ weirdShit.HomeGoals + '-' + weirdShit.AwayGoals : '' }
                        </Typography>
                    </CardContent>
                </Card>
                <Snackbar 
                    anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }} 
                    sx={{  mb:6 }}
                    open={open} 
                    autoHideDuration={3000} 
                    onClose={() => this.setState({open: false})}
                >
                    <Alert 
                    onClose={() => this.setState({open: false})} 
                    severity={saved.severity} 
                    sx={{ width: '100%' }}
                    >
                        {saved.message}
                    </Alert>
                </Snackbar>
            </ThemeProvider>
        );
    }
}

export default MatchCard;

