import  { Component } from "react";
import '../css/App.css';
import { Box, Button, createTheme, Snackbar, TextField, ThemeProvider } from "@mui/material";
import MuiAlert, { AlertProps } from '@mui/material/Alert';
import * as React from 'react';

const Alert = React.forwardRef<HTMLDivElement, AlertProps>(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const theme = createTheme({
  palette: {
      mode: "dark"
  }
});

type MyProps = { 
    currentUser?: any
    onLogout: any
};
type MyState = { 
  open: boolean, 
  saved: any 
};

class Account extends Component <MyProps, MyState>{
    
    constructor(props: MyProps) {
        super(props);
        this.state = {
          open: false,
          saved: {
              message: "Ventetekst!",             
              severity: "info"
          }
        };
    }

    handleClick = () => {
        this.setState({open:true})
    };

    handleClose = (event:any) => {
        if (event.reason === 'clickaway') {
            return;
        }
        this.setState({open:false})
    };

    handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
      const { currentUser } = this.props;
      event.preventDefault();
      const data = new FormData(event.currentTarget);
      // eslint-disable-next-line no-console
      const requestOptions = {
          method: 'POST',
          headers: { 
              'Content-Type': 'application/json',
              'x-auth-token': currentUser.userToken
          },
          body: JSON.stringify({
            roundId: data.get('roundId'),
          })
      };
      fetch('https://hattrick-webapi.herokuapp.com/api/tipping/updateRound', requestOptions)
      .then(response => {
        if(response.ok){
          this.setState({
            saved: {
                message: "Runde manuelt endret!", 
                severity: "success"
            },
            open:true
        })
        }
        return
      })
      .catch((error) => {
          this.setState({
              saved: {message: "Noe gikk galt! " + error.message, severity:"error"},
              open:true
          }) 
      });
    };
    
    render() {
        const { currentUser, onLogout } = this.props;
        const { open, saved } = this.state;

        return (
            <div> 
                <div className="userinfo"> 
                  Brukernavn: {currentUser.userName}
                </div>
                <div>
                  Epost: {currentUser.userEmail}
                </div>
                <div className="userinfo"> 
                  <Button className="userinfo" variant="outlined" color="error" onClick={onLogout}>Logg ut</Button>
                </div>
                {
                  currentUser.userId === '1'
                  ?
                  <ThemeProvider theme={theme}>
                    <Box component="form" onSubmit={this.handleSubmit} noValidate sx={{ mt: 1 }}>
                      <TextField
                      disabled={currentUser.userId !== '1'}
                      margin="normal"
                      id="roundId"
                      label="Runde"
                      name="roundId"
                      type="number"
                      inputProps={{ 
                          pattern:"[0-9]*" 
                      }}
                      autoComplete="off"
                      />
                      <Button
                          disabled={currentUser.userId !== '1'}
                          type="submit"
                          variant="contained"
                          color="success"
                          sx={{ mt: 1, mb: 0, width: '60%' }}>
                          Oppdater
                      </Button>
                  </Box>
                  <Snackbar 
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }} 
                  open={open} 
                  autoHideDuration={3000} 
                  onClose={() => this.setState({open: false})}
                  >
                    <Alert 
                    onClose={() => this.setState({open: false})}
                    severity={saved.severity} 
                    sx={{ width: '100%', mb:6 }}
                    >
                        {saved.message}
                    </Alert>
                  </Snackbar>
                </ThemeProvider>
                  :
                  ''
                }
              </div> 
        );
    }
}

export default Account;

