var helpers = {
  fetchMatches: function(round, token) {
    var url = 'https://hattrick-webapi.herokuapp.com/api/tipping/'+round

    return fetch(url, {
        headers: {
            'x-auth-token': token
            // 'Content-Type': 'application/x-www-form-urlencoded',
          }
    })
    .then(function(response) {
      return response.json()
    })
    .catch(function(error) {
      console.log('error', error)
    })
  },
  fetchAllMatches: function(token) {
    var url = 'https://hattrick-webapi.herokuapp.com/api/tipping/'

    return fetch(url, {
        headers: {
            'x-auth-token': token
            // 'Content-Type': 'application/x-www-form-urlencoded',
          }
    })
    .then(function(response) {
      return response.json()
    })
    .catch(function(error) {
      console.log('error', error)
    })
  },
  getAllUsers: function(token) {
    var url = 'https://hattrick-webapi.herokuapp.com/api/tipping/getUsers'
    return fetch(url, {
        headers: {
            'x-auth-token': token
          }
    })
    .then(function(response) {
      return response.json()
    })
    .catch(function(error) {
      console.log('error', error)
    })
  },
  }
  
module.exports = helpers;